.App {
  
}

.App-link {
  color: #61dafb;
}

.productCard {
  display: flex;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  /*height: 80vh;*/
}

.product {
  border: 2px solid;
  margin-left: 25px;
  margin-right: 25px;
  background-color: rgba(128, 128, 128, 0.473); 
  border-radius: 10px;
  width: 15rem;
  height: 600px;
}

.product img {
  width: 150px;
  height: auto;
}

.buttonBasket {
  margin-bottom: 8px;
  border-radius: 8px;
  border-color: rgba(0, 0, 0, 0.644);
  padding: 8px;
  background-color: #000000;
  color: rgb(199, 199, 199);
}

.moreInfo {
  color:blue;
}

.moreInfo:hover {
  cursor: pointer;
}

.cartParent {
  background-color: grey;
  display: flex;
}

.shoppingCart {
  margin: 10px;
  padding-left: 25px;
}

.thankyou {
  margin-left: 25px;
}